<template>
    <el-container>
        <el-aside class="left-group-aside" v-show="$getUserGroupSee()">
            <leftgroup
                :grouplist="groupCounts"
                @callGroup="callbackGroup"
            ></leftgroup>
        </el-aside>
        <el-main>
            <toolbar
                :buttonList="buttonList"
                @callFunction="callFunction"
                placeholder="监测对象、设备名称、设备编号过滤"
            ></toolbar>
            <!--列表-->
            <el-table
                :data="builds"
                :row-class-name="$tableRowClassName"
                v-loading="listLoading"
                style="width: 100%"
            >
                <!-- <el-table-column type="selection" width="50"> </el-table-column> -->
                <el-table-column type="index" width="80">
                    <template slot-scope="scope">
                        <div
                            v-text="(page - 1) * pageSize + 1 + scope.$index"
                        ></div>
                    </template>
                </el-table-column>
                <el-table-column prop="groupshortname" label="监测对象分组">
                </el-table-column>
                <el-table-column prop="siteshortname" label="监测对象">
                </el-table-column>
                <el-table-column
                    prop="dpcode"
                    label="深基坑编号"
                ></el-table-column>

                <el-table-column prop="dpname" label="深基坑名称">
                </el-table-column>

                <el-table-column
                    label="操作"
                    width="150"
                    v-if="canEdit || canDelete"
                >
                    <template slot-scope="scope">
                        <el-button
                            v-if="canEdit"
                            plain
                            type="warning"
                            size="small"
                            @click="handleEdit(scope.$index, scope.row)"
                            >编辑</el-button
                        >
                        <el-button
                            v-if="canDelete"
                            plain
                            type="danger"
                            size="small"
                            @click="handleDel(scope.$index, scope.row)"
                            >删除</el-button
                        >
                    </template>
                </el-table-column>
            </el-table>

            <!--工具条-->
            <el-col :span="24" class="toolbar">
                <el-pagination
                    layout="total, prev, pager, next, jumper"
                    @current-change="handleCurrentChange"
                    :page-size="pageSize"
                    :current-page="page"
                    :total="total"
                    style="float: right"
                >
                </el-pagination>
            </el-col>

            <!--编辑界面-->
            <el-dialog
                title="编辑"
                :visible.sync="editFormVisible"
                v-model="editFormVisible"
                :close-on-click-modal="false"
                class="file-dialog"
            >
                <el-form
                    :model="editForm"
                    label-width="120px"
                    :rules="editFormRules"
                    ref="editForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="editForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="editForm.SITEID"
                                filterable
                                placeholder="请选择"
                                @change="siteChange"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="深基坑名称" prop="dpname">
                            <el-input
                                v-model="editForm.dpname"
                                auto-complete="off"
                                maxlength="40"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="深基坑编号" prop="dpcode">
                            <el-input
                                v-model="editForm.dpcode"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-row>
                            <el-col :span="16">
                                <el-form-item label="经纬度" prop="longitude"
                                    ><el-input
                                        v-model="editForm.longitude"
                                        auto-complete="off"
                                        maxlength="30"
                                    ></el-input></el-form-item
                            ></el-col>
                            <el-col :span="8">
                                <el-form-item prop="latitude" label-width="0">
                                    <el-input
                                        v-model="editForm.latitude"
                                        auto-complete="off"
                                        maxlength="30"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-col :span="24">
                            <el-form-item label="上传图片" prop="dpurl">
                                <el-upload
                                    ref="imgEditFileUpload"
                                    class="upload-demo"
                                    :class="{ disabled: uploadEditDisabled }"
                                    action="fakeaction"
                                    accept=".jpg"
                                    list-type="picture-card"
                                    :multiple="false"
                                    :limit="1"
                                    :file-list="editForm.dpurl"
                                    :on-change="imgEditFileChanged"
                                    :http-request="uploadEditImgFile"
                                    :on-remove="removeEditImgFile"
                                >
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{ file }">
                                        <el-image
                                            class="
                                                el-upload-list__item-thumbnail
                                            "
                                            :src="file.url"
                                            fit="cover"
                                        ></el-image>
                                        <span
                                            class="el-upload-list__item-actions"
                                        >
                                            <span
                                                class="
                                                    el-upload-list__item-preview
                                                "
                                                @click="handleOpenFile(file)"
                                            >
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span
                                                class="
                                                    el-upload-list__item-delete
                                                "
                                                @click="
                                                    handleEditRemoveImgFile(
                                                        file
                                                    )
                                                "
                                            >
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                            </el-form-item>
                        </el-col>
                    </el-col>
                    <el-col :span="12">
                        <mark-map
                            ref="myMap"
                            v-if="editFormVisible"
                            :markposition="{
                                lng: editForm.longitude,
                                lat: editForm.latitude,
                            }"
                            :addrinput="{}"
                            @callMap="callEditMap"
                        ></mark-map>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="editFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="editSubmit"
                        :loading="editLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>

            <!--新增界面-->
            <el-dialog
                title="新增"
                :visible.sync="addFormVisible"
                v-model="addFormVisible"
                :close-on-click-modal="false"
                class="file-dialog"
            >
                <el-form
                    :model="addForm"
                    label-width="120px"
                    :rules="addFormRules"
                    ref="addForm"
                >
                    <el-col :span="12">
                        <el-form-item label="监测对象分组" prop="GROUPID">
                            <el-select
                                v-model="addForm.GROUPID"
                                filterable
                                placeholder="请选择"
                                @change="groupChange"
                            >
                                <el-option
                                    v-for="item in groups"
                                    :key="item.GROUPID"
                                    :label="item.groupshortname"
                                    :value="item.GROUPID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="监测对象" prop="SITEID">
                            <el-select
                                v-model="addForm.SITEID"
                                filterable
                                placeholder="请选择"
                                @change="siteChange"
                            >
                                <el-option
                                    v-for="item in selectSites"
                                    :key="item.SITEID"
                                    :label="item.siteshortname"
                                    :value="item.SITEID"
                                >
                                </el-option>
                            </el-select>
                        </el-form-item>
                    </el-col>

                    <el-col :span="12">
                        <el-form-item label="深基坑名称" prop="dpname">
                            <el-input
                                v-model="addForm.dpname"
                                auto-complete="off"
                                maxlength="40"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-form-item label="深基坑编号" prop="dpcode">
                            <el-input
                                v-model="addForm.dpcode"
                                auto-complete="off"
                                maxlength="50"
                            ></el-input>
                        </el-form-item>
                    </el-col>
                    <el-col :span="12">
                        <el-row>
                            <el-col :span="16">
                                <el-form-item label="经纬度" prop="longitude"
                                    ><el-input
                                        v-model="addForm.longitude"
                                        auto-complete="off"
                                        maxlength="30"
                                    ></el-input></el-form-item
                            ></el-col>
                            <el-col :span="8">
                                <el-form-item prop="latitude" label-width="0">
                                    <el-input
                                        v-model="addForm.latitude"
                                        auto-complete="off"
                                        maxlength="30"
                                    ></el-input>
                                </el-form-item>
                            </el-col>
                        </el-row>
                        <el-col :span="24">
                            <el-form-item label="上传图片" prop="dpurl">
                                <el-upload
                                    ref="imgFileUpload"
                                    class="upload-demo"
                                    :class="{ disabled: uploadAddDisabled }"
                                    action="fakeaction"
                                    accept=".jpg"
                                    list-type="picture-card"
                                    :multiple="false"
                                    :limit="1"
                                    :file-list="addForm.dpurl"
                                    :on-change="imgFileChanged"
                                    :http-request="uploadImgFile"
                                    :on-remove="removeImgFile"
                                >
                                    <i slot="default" class="el-icon-plus"></i>
                                    <div slot="file" slot-scope="{ file }">
                                        <el-image
                                            class="
                                                el-upload-list__item-thumbnail
                                            "
                                            :src="file.url"
                                            fit="cover"
                                        ></el-image>
                                        <span
                                            class="el-upload-list__item-actions"
                                        >
                                            <span
                                                class="
                                                    el-upload-list__item-preview
                                                "
                                                @click="handleOpenFile(file)"
                                            >
                                                <i class="el-icon-zoom-in"></i>
                                            </span>
                                            <span
                                                class="
                                                    el-upload-list__item-delete
                                                "
                                                @click="
                                                    handleRemoveImgFile(file)
                                                "
                                            >
                                                <i class="el-icon-delete"></i>
                                            </span>
                                        </span>
                                    </div>
                                </el-upload>
                            </el-form-item>
                        </el-col>
                    </el-col>
                    <el-col :span="12">
                        <mark-map
                            ref="myMap"
                            v-if="addFormVisible"
                            :markposition="{
                                lng: addForm.longitude,
                                lat: addForm.latitude,
                            }"
                            :addrinput="{}"
                            @callMap="callAddMap"
                        ></mark-map>
                    </el-col>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click.native="addFormVisible = false"
                        >取消</el-button
                    >
                    <el-button
                        type="primary"
                        @click.native="addSubmit"
                        :loading="addLoading"
                        >提交</el-button
                    >
                </div>
            </el-dialog>
        </el-main>
    </el-container>
</template>
<script>
import {
    getdppagelist,
    getdpGroups,
    removedp,
    editdp,
    adddp,
    checkdpcode,
    postfile,
} from "../../api/api";
import leftgroup from "../../components/LeftGroup";
import { getButtonList } from "../../promissionRouter";
import Toolbar from "../../components/Toolbar";
import dicmixin from "../../assets/dic/dicmixin";
import MarkMap from "../../components/MarkMap";
import { mfogtype } from "../../assets/dic/dic";
export default {
    components: { leftgroup, Toolbar, MarkMap },
    mixins: [dicmixin],
    data() {
        return {
            groupCounts: [],
            buttonList: [],
            filters: {
                name: "",
            },
            builds: [],
            total: 0,
            page: 1,
            pageSize: 10,
            listLoading: false,
            addDialogFormVisible: false,
            editFormVisible: false, //编辑界面是否显示
            editLoading: false,
            editFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                longitude: [
                    {
                        required: true,
                        message: "请填写经度",
                        trigger: "blur",
                    },
                ],
                latitude: [
                    {
                        required: true,
                        message: "请填写纬度",
                        trigger: "blur",
                    },
                ],
                dpname: [
                    {
                        required: true,
                        message: "请填写深基坑名称",
                        trigger: "blur",
                    },
                ],
                dpurl: [
                    {
                        required: true,
                        message: "请上传图片",
                        trigger: "blur",
                    },
                ],
                dpcode: [
                    {
                        required: true,
                        message: "请填写深基坑编号",
                        trigger: "blur",
                    },
                    {
                        message: "深基坑编号不能重复",
                        trigger: "blur",
                        validator: async (rule, value, callback) => {
                            var result = await checkdpcode({
                                code: this.editForm.dpcode,
                                groupid: this.editForm.GROUPID,
                                dpid: this.editForm.DPID,
                            });
                            if (result.data) {
                                callback();
                            } else {
                                callback(new Error(""));
                            }
                        },
                    },
                ],
            },
            editForm: {
                dpurl: [],
            },
            addFormVisible: false,
            addLoading: false,
            addFormRules: {
                GROUPID: [
                    {
                        required: true,
                        message: "请选择监测对象分组",
                        trigger: "blur",
                    },
                ],
                SITEID: [
                    {
                        required: true,
                        message: "请选择监测对象",
                        trigger: "blur",
                    },
                ],
                longitude: [
                    {
                        required: true,
                        message: "请填写经度",
                        trigger: "blur",
                    },
                ],
                latitude: [
                    {
                        required: true,
                        message: "请填写纬度",
                        trigger: "blur",
                    },
                ],
                dpname: [
                    {
                        required: true,
                        message: "请填写深基坑名称",
                        trigger: "blur",
                    },
                ],
                dpurl: [
                    {
                        required: true,
                        message: "请上传图片",
                        trigger: "blur",
                    },
                ],
                dpcode: [
                    {
                        required: true,
                        message: "请填写深基坑编号",
                        trigger: "blur",
                    },
                    {
                        message: "深基坑编号不能重复",
                        trigger: "blur",
                        validator: async (rule, value, callback) => {
                            var result = await checkdpcode({
                                code: this.addForm.dpcode,
                                groupid: this.addForm.GROUPID,
                            });
                            if (result.data) {
                                callback();
                            } else {
                                callback(new Error(""));
                            }
                        },
                    },
                ],
            },
            addForm: { dpurl: [] },
            canEdit: false,
            canDelete: false,
            groups: [],
            selectSites: [],
            selectGroupId: 0,
            mfogtype: mfogtype,
        };
    },
    computed: {
        uploadEditDisabled: function () {
            return this.editForm.dpurl.length > 0;
        },
        uploadAddDisabled: function () {
            return this.addForm.dpurl.length > 0;
        },
    },
    methods: {
        handleRemoveImgFile() {
            this.$refs["imgFileUpload"].clearFiles();
            this.addForm.dpurl = [];
            this.addForm.fileid = "";
        },
        handleEditRemoveImgFile() {
            this.$refs["imgEditFileUpload"].clearFiles();
            this.editForm.dpurl = [];
            this.editForm.fileid = "";
        },
        removeImgFile(file, fileList) {
            this.addForm.dpurl = [];
            this.addForm.fileid = "";
        },
        removeEditImgFile(file, fileList) {
            this.editForm.dpurl = [];
            this.editForm.fileid = "";
        },
        uploadImgFile(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    this.addForm.fileid = res.data;
                })
                .catch((err) => {});
        },
        uploadEditImgFile(param) {
            let fileObj = param.file; // 文件对象
            let form = new FormData(); // FormData 对象
            form.append("formFiles", fileObj);
            postfile(form)
                .then((res) => {
                    this.editForm.fileid = res.data;
                })
                .catch((err) => {});
        },
        imgFileChanged(file, fileList) {
            this.addForm.dpurl = fileList;
        },
        imgEditFileChanged(file, fileList) {
            this.editForm.dpurl = fileList;
        },
        callbackGroup(item) {
            this.selectGroupId = item.GROUPID;
            this.page = 1;
            this.getPageList();
        },
        callAddMap(item) {
            this.$set(this.addForm, "longitude", item.point.lng);
            this.$set(this.addForm, "latitude", item.point.lat);
        },
        callEditMap(item) {
            this.editForm.longitude = item.point.lng;
            this.editForm.latitude = item.point.lat;
        },
        callFunction(item) {
            this.filters = {
                name: item.search,
            };
            this[item.Func].apply(this, item);
        },
        getPageList(index) {
            if (index) {
                this.page = index;
            } else {
                this.page = 1;
            }
            let paras = {
                groupid: this.selectGroupId,
                keyword: this.filters.name,
                page: this.page,
                size: this.pageSize,
            };
            this.listLoading = true;
            getdppagelist(paras).then((res) => {
                if (res.success) {
                    let tmpdata = res.data.data || [];

                    this.builds = tmpdata.map((t) => {
                        t.dpurl = [{ name: t.dpurl, url: t.dpurl }];
                        return t;
                    });
                    this.total = res.data.dataCount;
                }
                this.listLoading = false;
            });
        },
        handleCurrentChange(val) {
            this.getPageList(val);
        },
        getGroupCounts() {
            getdpGroups().then((res) => {
                if (res.success) {
                    this.groupCounts = res.data;
                }
            });
        },
        groupChange(val) {
            this.getGroupSites(val);
            this.$set(this.addForm, "SITEID", "");
            this.$set(this.editForm, "SITEID", "");
            this.selectSites = this.groupSites;
        },
        siteChange(val) {
            let sites = this.selectSites.filter((res) => {
                return res.SITEID == val;
            });

            if (sites && sites.length > 0) {
                this.$refs.myMap.changeLoc(
                    sites[0].parentlng,
                    sites[0].parentlat
                );
                if (this.addFormVisible) {
                    this.$set(this.addForm, "longitude", sites[0].sitelng);
                    this.$set(this.addForm, "latitude", sites[0].sitelat);
                } else if (this.editFormVisible) {
                    this.$set(this.editForm, "longitude", sites[0].parentlng);
                    this.$set(this.editForm, "latitude", sites[0].parentlat);
                }
            }
        },
        //删除
        handleDel(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要删除的一行数据！",
                    type: "error",
                });

                return;
            }
            this.$confirm("确认删除该记录吗?", "提示", {
                type: "warning",
            })
                .then(() => {
                    this.listLoading = true;
                    let para = { id: row.DPID };
                    removedp(para).then((res) => {
                        this.listLoading = false;
                        if (res.success) {
                            this.$message({
                                message: "删除成功",
                                type: "success",
                            });
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.getPageList(this.page);
                        this.getGroupCounts();
                    });
                })
                .catch(() => {});
        },
        //显示编辑界面
        async handleEdit(index, r) {
            let row = r;
            if (!row) {
                this.$message({
                    message: "请选择要编辑的一行数据！",
                    type: "error",
                });
                return;
            }
            this.editFormVisible = true;
            this.groupChange(row.GROUPID);
            this.editForm = Object.assign({}, row);
        },
        //显示新增界面
        handleAdd() {
            this.addFormVisible = true;
            this.addForm = { dpurl: [] };
            if (this.$refs["addForm"]) {
                this.$refs["addForm"].clearValidate();
            }
        },
        handleOpenFile(file) {
            window.open(file.url);
        },
        //编辑
        editSubmit: function () {
            this.$refs.editForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.editLoading = true;
                    let { dpurl, ...para } = Object.assign({}, this.editForm);

                    para.dpurl = dpurl.map((t) => t.url).join();
                    editdp(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "修改成功",
                                type: "success",
                            });
                            this.$refs["editForm"].resetFields();
                            this.editFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }

                        this.editLoading = false;
                    });
                    //});
                }
            });
        },
        //新增
        addSubmit: function () {
            let _this = this;
            this.$refs.addForm.validate((valid) => {
                if (valid) {
                    //this.$confirm("确认提交吗？", "提示", {}).then(() => {
                    this.addLoading = true;
                    let { dpurl, ...para } = Object.assign({}, this.addForm);
                    para.dpurl = dpurl.map((t) => t.name).join();

                    adddp(para).then((res) => {
                        if (res.success) {
                            this.$message({
                                message: "新增成功",
                                type: "success",
                            });
                            this.$refs["addForm"].resetFields();
                            this.addFormVisible = false;
                            this.getPageList(this.page);
                            this.getGroupCounts();
                        } else {
                            this.$message({
                                message: res.msg,
                                type: "error",
                            });
                        }
                        this.addLoading = false;
                    });
                    //});
                }
            });
        },
    },
    mounted() {
        this.getPageList();
        this.getGroupCounts();
        this.getGroups();
        let routers = window.sessionStorage.router
            ? JSON.parse(window.sessionStorage.router)
            : [];
        let buttons = getButtonList(this.$route.path, routers);
        buttons.forEach((element) => {
            //&& element.IsButton && !element.IsHide
            if (element.Func == "handleEdit") {
                this.canEdit = true;
            } else if (element.Func == "handleDel") {
                this.canDelete = true;
            } else {
                this.buttonList.push(element);
            }
        });
    },
};
</script>
<style scoped>
.left-group {
    padding: 0px 10px;
}
.el-select {
    width: 100%;
}
.file-dialog /deep/ .el-upload-list__item {
    transition: none !important;
}
.disabled /deep/ .el-upload--picture-card {
    display: none !important;
}
.bm-view {
    padding-left: 10px;
    width: 100%;
    height: 222px;
    margin-bottom: 10px;
}
.file-dialog /deep/ .el-upload-list__item > div {
    height: 100%;
}
</style>


